import React from 'react';

const HomePresentation = () => {
  return (
    <div className="home-blurb-container">

      <img src={require('../images/logo.svg')} className="home-guarda-logo" />

      <div className="home-guarda-records">
        <img src={require('../images/records.svg')} className="records" />
      </div>

      <div className="home-guarda-blurb">
        East London immersive vibrations... Smooth, bassy, juicy beats!
      </div>

      <div className="home-guarda-social">

        <a href="https://www.facebook.com/guardarecords/"><img src={require('../images/social_fb.svg')} className="social_img" /></a>
        <a href="https://twitter.com/guardarecords/"><img src={require('../images/social_twitter.svg')} className="social_img" /></a>
        <a href="https://www.instagram.com/guardarecords/"><img src={require('../images/social_instagram.svg')} className="social_right_img" /></a>

      </div>

      <div className="clear" />

    </div>
  );
};

export default HomePresentation;


