import React, {PropTypes} from 'react';

import RadioPlayer from './RadioPlayer';
import ControlButton from './ControlButton';
import OnAirLabel from './OnAirLabel';

const RadioPresentation = props => {
  return (
    <div className="radio-player-container">

      <RadioPlayer
        stream={props.streamUrl}
        onPlay={props.onPlay}
        paused={props.paused}/>

      <div>

        <img src={require('../../images/logo.svg')} className="guarda-icon" />

      </div>

      <div className="presents">
        
        <img src={require('../../images/presents.svg')} />

      </div>

      <OnAirLabel
        onAir={props.onAir}/>

      <ControlButton
        onStartClick={props.onStartClick}
        onPauseClick={props.onPauseClick}
        paused={props.paused} />

    </div>
  );
};

RadioPresentation.propTypes = {
  streamUrl: PropTypes.string.isRequired,
  onPlay: PropTypes.func.isRequired,
  paused: PropTypes.bool.isRequired,
  onAir: PropTypes.string.isRequired,
  onStartClick: PropTypes.func.isRequired,
  onPauseClick: PropTypes.func.isRequired
};

export default RadioPresentation;



